<template>
    <div class="examination students">
        <!-- 考核方式 -->
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/trainingProgram/examination' }">培养方案</el-breadcrumb-item>
                <el-breadcrumb-item>
                考核方式管理
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="examination-dv">
            <img src="@/assets/mao.png" alt="">
            <span>考核方式管理</span>
        </div>
        <div class="examination-btn">
            <el-button type="primary" round @click="close">+ 新建</el-button>
        </div>
        <div class="examination-tab">
            <template>
                <el-table :data="tableData" border stripe style="width: 100%"  v-loading="loading">
                    <el-table-column label="考核方式名称" width="200">
                        <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.name }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="考核方式描述" >
                        <template slot-scope="scope">
                        <span
                            style="margin-left: 10px;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;"
                        >{{ scope.row.description }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作" width="140">
                        <template slot-scope="scope">
                        <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row)">
                            <i class="el-icon-edit"></i>&nbsp;&nbsp;编辑
                        </el-button>
                        <el-button type="text" size="small" @click="handleDelete(scope.$index, scope.row)">
                            <i class="el-icon-delete"></i>&nbsp;&nbsp;删除
                        </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </template>
        </div>
        <!-- 弹框组件 -->
        <div class="examination-dia">
            <el-dialog :title="tianjia" :visible.sync="dialogFormVisible" :before-close="handleClose" :close-on-click-modal='false'>
                <el-form :model="form" :rules="rules" ref="ruleForm">
                    <!-- <span class="ssss">考核方式名称：</span> -->
                    <el-form-item   prop="user"  label="考核方式名称：">
                        <!-- <span class="ssss">考核方式名称：</span>
                        <el-select v-model="value" placeholder="" class="pppp">
                            <el-option
                                v-for="item in options"
                                :key="item.id"
                                :label="item.name"
                                :value="item.name"
                            ></el-option>
                        </el-select> -->
                        <el-input v-model="form.user" placeholder="考核方式名称："></el-input>
                    </el-form-item>
                       
                    <el-form-item label="考核方式描述："  prop="description">
                        <el-input
                                type="textarea"
                                :rows="2"
                                placeholder="请输入内容"
                                v-model="form.description">
                        </el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('ruleForm')">取 消</el-button>
                    <el-button type="primary" @click="determine('ruleForm')" v-show="falg1">确 定</el-button>
                    <el-button type="primary" @click="determine1" v-show="falg">确 定</el-button>
                </div>
            </el-dialog>
        </div>
        <div>
            <template>
                <div class="block">
                <el-pagination
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageNum"
                    :page-sizes="[15, 30, 40, 50]"
                    :page-size="pageSize"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="totals"
                ></el-pagination>
                </div>
            </template>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            pageSize:15,
            pageNum:1,
            totals:0,
            dialogFormVisible:false,
            formLabelWidth: '120px',
            loading:false,
            tianjia:'',
            falg1:false,
            falg:false,
            textarea:'',
            tableData:[],
             form: {
                name: '',
                id:'',
                description:'',
                user:''
            },
            rules:{
                user: [{required: true, message: '考核方式不能为空', trigger: 'blur'}],
            },
            options:[],
            value: '',
        }
    },
    methods:{
        close(){
            this.falg1 = true
            this.falg = false
            this.tianjia = '添加考核方式'
            this.dialogFormVisible = true
            this.form = {}
            this.value = ''
        },
        ///basic/examine/list   /teacher/CourseInformation/course?courseId='+id
        examineList(){
            let key = {
                page :this.pageNum,
                rows:this.pageSize
            }
            this.$post(`basic/examine/list`,key).then(res=>{
                if(res.status == 1){
                    this.tableData = res.data
                    this.totals = res.total
                }
                
            })
        },
        // /basic/examine/all
        allexamine(){
            this.$get('/basic/examine/all').then(res=>{
                if(res.status == 1){
                    this.options = res.data
                }
            })
        },
        // /basic/examine/add
        addexamine(){
            let key ={
                name:this.form.user,
                description:this.form.description
            }
            this.$post('/basic/examine/add',key).then(res=>{
                if(res.status == 1){
                    this.examineList()
                }
            })
        },
        determine(ruleForm){
            this.$refs[ruleForm].validate((valid) => {
                if (valid) {
                    this.addexamine()
                    this.dialogFormVisible = false
                } else {
                    return false;
                }
            });
        },
        determine1(){
            let key = {
                id:this.form.id,
                name:this.form.user,
                description:this.form.description
            }
            this.$put('/basic/examine/edit',key).then(res=>{
                if(res.status == 1){
                    this.form.user = res.data.name
                    this.form.description = res.data.description
                }
                this.examineList()
            })
            this.dialogFormVisible = false
        },
        resetForm(ruleForm) {
            this.$refs[ruleForm].resetFields();
            this.dialogFormVisible = false
        },
        handleClose(){
            this.$refs['ruleForm'].resetFields();
            this.dialogFormVisible = false
        },
        handleEdit(index,row){
            this.falg1 = false
            this.falg = true
            this.tianjia = '编辑考核方式'
            this.dialogFormVisible = true
            this.form.user = row.name
            this.form.id = row.id
            this.form.description = row.description
        },
        handleDelete(index,row){
             this.$confirm('确定删除该信息?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$deletes(`/basic/examine/del/${row.id}`).then(res=>{
                    if(res.status == 1){
                        this.examineList()
                        this.$notify({
                            message: '删除成功',
                            type: 'success'
                        });
                    }else {
                        this.$notify({
                            message: res.message,
                            type: 'warning'
                        });
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            }); 
            
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.examineList()
        },
        handleCurrentChange(val) {
            this.pageNum = val;
            this.examineList();
        }
    },
    mounted(){
        this.examineList()
        this.allexamine()
    }
}
</script>
<style lang="scss" >
    .examination{
        padding: 0 25px;
        .examination-dv{
            float: left;
            text-align: left;
            margin-left: 20px;
            img{
                float: left;
                margin-top: 3px;
                margin-right: 6px;
            }
        }
        .examination-btn{
            float: right;
            margin-top: -3px;
        }
        .examination-tab{
            margin-top: 35px;
            background-color: #fff;
            padding: 0 15px;
            padding-top: 15px;
             box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.078);
        }
        .block{
            text-align: right;
            background-color: #fff;
            padding-top: 15px;
            padding-bottom: 15px;
            box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.078);
        }
        .ssss{
            float: left;
            margin-right: 6px;
            margin-left: 10px;
            font-weight:bold;
            color: #696969;
            font-size: 14px;
        }
        .examination-dia{
            float: left;
            width: 100%;

                .pppp{
                    width: 78%;
                    float: left;
                    margin: 0;
                }
                .el-dialog{
                    width: 31%;
                }
                .el-textarea{
                    width: 78%;
                }
                .el-form-item__label{
                    padding-right: 2px;
                }
                .el-input{
                    width: 79%;
                }
        }
    }
</style>



